/**
 * SectionScroll
 * @constructor
 */
class SectionScroll {
    constructor(root) {
        if (!root) {
            return;
        }

        this.root = root;
        this.sections = this.root.querySelectorAll('.js-section-scroll__item');
        this.sectionsInner = this.root.querySelectorAll('.js-section-scroll__inner');
        this.sectionsLength = this.sections.length;
        this.sectionWidth = window.innerWidth;
        this.isCurrent = 1;

        this.setInitStyle();
        this.fitSectionSize();
        this.resizeEvent();
        this.wheelEvent();
        // this.setIntersectionObserver();

        setTimeout(() => {
            this.root.style.transform = `translateY(-${this.sectionHeight}px)`
            this.isCurrent = 1;
        }, 1000);
    }

    setInitStyle() {
        this.root.style.transition = 'transform .5s';
        this.root.style.transitionTimingFunction = 'ease';
        this.sections.forEach((element) => {
            element.style.position = 'fixed';
            element.style.top = '0';
            element.style.bottom = '0';
        });
    }

    fitSectionSize() {
        this.sectionWidth = window.innerWidth;
        this.sectionHeight = window.innerHeight;

        for (let i = 0; i < this.sectionsLength; i++) {
            this.sections[i].style.width = `${this.sectionWidth}px`;
            this.sections[i].style.height = `${this.sectionHeight}px`;
            this.sectionsInner[i].style.height = `${this.sectionHeight}px`;
            this.sections[i].style.transform = `translateY(${this.sectionHeight * i}px)`
        }
    }

    resizeEvent() {
        let timeId = 0;

        window.addEventListener('resize', () => {
            if (timeId) {
                return;
            }

            timeId = setTimeout(() => {
                timeId = 0;
                this.fitSectionSize();
            }, 200);
        });
    }

    wheelEvent() {
        let timeId = 0;
        let startPosition = 0;

        window.addEventListener('wheel', (e) => {
            if (timeId) {
                return;
            }

            timeId = setTimeout(() => {
                timeId = 0;
                console.log(e)
            }, 1000);
        });
    }


    // setIntersectionObserver() {
    //     const options = {
    //         root: null,
    //         rootMargin: '0px',
    //         threshold: 0.1
    //     }

    //     const setCallback = (entries, object) => {
    //         entries.forEach((entry) => {
    //             console.log(entry);
    //         });
    //     }

    //     const observer = new IntersectionObserver(setCallback, options);
    //     this.sectionsInner.forEach((element) => {
    //         observer.observe(element);
    //     })
    // }
}

export default function secrionScroll() {
    document.querySelectorAll('.js-section-scroll').forEach((element) => new SectionScroll(element));
}